@media(max-width: 1200px){

  .landing.section{
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100%;
  }

  .landing-logo-wrapper{
    max-width: 70%;
    margin: 0 auto;

  }
  .landing-avatar{
    max-width:100%;
  }

  .menu-wrapper{
    display: flex;
    justify-content:space-around;
    max-width: 100%;
    margin-top: 6.2rem;
    font-size: 1.5rem;
